import React, { Component } from 'react';
import {ProgressBar, Button} from 'react-bootstrap';
class PlanLimit extends Component{
    render(){
        const {title, min , max} = this.props
        const now = max == 0 ? 0 : (min/max) * 100
        return <div className='mb-7'>
                    <div className='d-flex justify-content-between'>
                        <p className='mb-2 fw-medium'>{`${max==0? `Unlimited ${title}`:`${min}/${max} ${title}`}`}</p>
                        {now >= 80 ? <Button variant='link' size={'sm'} className='text-danger p-0 mb-2 fw-bold' onClick={window.Intercom ? (e) => { window.Intercom('show') } : () => {}}>Upgrade : Talk to Sales</Button> : null}
                    </div>
                    <ProgressBar now={now}/>
                </div>
    }
}

export default PlanLimit